.sideBarBox {
  display: none;
  @media screen and (min-width: 1181px) {
    display: block;
    flex: 0 0 260px;

    min-height: 240px;
    height: 100%;
  }
}
.allUsersContainer {
  width: 100%;
  padding-bottom: 40px;
  @media screen and (min-width: 1181px) {
    padding-left: 30px;
  }
}
.allUserItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  padding: 6.3px 20px;
  background: white;
  border-radius: 6px;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}
.allUsersSearchbody {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: space-between;
  justify-content: center;
  margin-bottom: 20px;
  padding: 0px 15px;
  @media screen and (min-width: 1181px) {
    padding: 0px 0px;
  }
}

.errorMessage {
  color: rgb(255, 130, 130);
  font-size: 16px;
  text-align: center;
}
.allUsersBth {
  cursor: pointer;
  color: black;
  text-decoration: none;
  border-bottom: 1px solid black;
  transition: color 250ms linear, border-bottom 250ms linear;
  &:hover {
    color: gray;
    border-bottom: 1px solid gray;
  }
}
.openAllUsers {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  border: none;
  background: rgb(110, 116, 161);
  color: white;

  border-radius: 50%;

  outline: none;
  font-size: 14px;

  flex: 0 0 40px;
  margin-left: 15px;
  height: 40px;
  transition: background-color 250ms linear;
  &:hover {
    background-color: rgb(255, 118, 118);
  }
}
.closeAllUsers {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  border: none;
  background: rgb(110, 116, 161);
  color: white;
  border-radius: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  outline: none;

  width: 40px;
  height: 40px;
  transition: background-color 250ms linear;
  &:hover {
    background-color: rgb(255, 118, 118);
  }
}
